.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

input {
  margin-right: 10px;
  outline: none;
  background-color: white;
  padding: 6px 10px;
  border: 1px solid black;
  border-radius: 10px;
}

button {
  margin-top: 10px;
  outline: none;
  background-color: white;
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 10px;
  cursor: pointer;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.black-nav h4 {
  font-size: 20px;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
  position: relative;
}

.list span {
  cursor: pointer;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eeeeee;
  text-align: left;
}

button.delete {
  position: absolute;
  top: 0;
  right: 20px;
}